.expert {
    &_title {
        margin-top: 115px;
    }
    &_desc {
        margin: 5px auto 22px;
        text-align: center;
        font-size: 30px;
        font-weight: 700;
    }
    &_wrap {
        display: flex;
        justify-content: space-around;
    }
    &_item {
        text-align: center;
        &--title {
            margin-top: 10px;
            font-size: 14px;
            font-weight: 700;
            text-transform: uppercase;
        }
        &--desc {
            display: block;
            margin: 5px auto 10px;
            font-size: 14px;
            font-weight: 700;
        }
        &--btn {
            display: inline-block;
            background-color: #b72b45;
            border-radius: 25px;
            padding: 7px 27px;
            font-size: 25px;
            font-weight: 700;
            text-transform: uppercase;
            color: #fff;
        }
    }
    &_h3 {
        text-align: center;
        margin-top: 65px;
        font-size: 54px;
        font-weight: 700;
        color: #b72b45;
    }
    &_text {
        margin-top: 20px;
        text-align: center;
        font-weight: 700;
        font-size: 30px;
    }
}