/* HTML5 display-role reset for older browsers */

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

ol,
ul {
  list-style: none;
}

li {
  list-style: none;
}

:active,
:hover,
:focus {
  outline: 0;
  outline-offset: 0;
}

input {
  border: none;
}

button {
  background: none;
  border: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

:active,
:hover,
:focus {
  outline: 0;
  outline-offset: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

a:hover {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

.btn {
  display: inline-block;
  text-align: center;
  padding: 4px 42px 6px;
  background-color: #fff;
  border: 3px solid #b72b45;
  border-radius: 25px;
  color: #b72b45;
  font-size: 15px;
  font-weight: 700;
  transition: .3s all;
}

.btn:hover {
  background-color: #b72b45;
  color: #fff;
}

.h2 {
  text-align: center;
  font-size: 65px;
  font-weight: 700;
  text-transform: uppercase;
}

.form_wrap {
  display: flex;
  justify-content: space-between;
}

.form_input {
  width: 30%;
  padding: 17px 25px 14px;
  border: 4px solid #b72b45;
  border-radius: 25px;
}

.form_input::placeholder {
  color: #000;
  font-size: 24px;
  font-weight: 300;
  text-transform: uppercase;
}

.form_btn {
  display: block;
  text-align: center;
  margin: 35px auto 0;
  padding: 12px 52px;
  background-color: #b72b45;
  border-radius: 25px;
  color: #fff;
  font-size: 23px;
  font-weight: 700;
  text-transform: uppercase;
}

.btn2 {
  border: 3px solid #b72b45;
  transition: .3s all;
}

.btn2:hover {
  background-color: #fff;
  color: #b72b45;
}

.overlay {
  position: fixed;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.8);
  display: block;
}

.popup {
  position: fixed;
  width: 400px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  padding: 40px;
  box-sizing: border-box;
  background-color: #fff;
  color: #000;
}

.close-popup {
  position: absolute;
  top: 15px;
  right: 15px;
  width: 23px;
  height: 23px;
  cursor: pointer;
}

.close-popup:before {
  content: '';
  background-color: #000;
  position: absolute;
  height: 1px;
  width: 31px;
  top: 11px;
  left: -4px;
  transform: rotate(-45deg);
}

.close-popup:after {
  content: '';
  background-color: #000;
  position: absolute;
  height: 1px;
  width: 31px;
  top: 11px;
  transform: rotate(45deg);
  left: -4px;
}

.popup_title {
  color: #b72b45;
  font-weight: 700;
  font-size: 25px;
  text-align: center;
}

.popup_desc {
  text-align: center;
  padding: 15px 0 25px;
}

.popup_wrap {
  flex-wrap: wrap;
}

.popup_input {
  width: 100%;
  margin: 10px 0;
}

.popup_btn {
  font-size: 18px;
}

.header_logo {
