.reviews {
    background: url(../img/reviews.png) no-repeat;
    background-position: center center;
    background-size: cover;
    min-height: 490px;
    margin-top: 85px;
    &_title {
        color: #ffffff;
        text-align: center;
        padding-top: 40px;
        font-size: 65px;
        font-weight: 700;
        text-transform: uppercase;
    }
    &_content {
        margin-top: 60px;
    }
    &_bottom {
        margin-top: 60px;
        text-align: center;
    }
    &_btn {
        padding: 15px 30px;
        text-transform: uppercase;
        &:last-child {
            margin-left: 115px;
        }
    }
}