.contact {
    min-height: 595px;
    position: relative;
    &_content {
        background-color: rgba(255,255,255,0.7);
        min-height: 252px;
        min-width: 355px;
        position: absolute;
        margin-top: 50px;
        padding: 0 12px;
        z-index: 1;
    }
    &_title {
        position: absolute;
        margin: 20px auto 30px;
        text-align: center;
        font-size: 37px;
        font-weight: 700;
        text-transform: uppercase;
    }
    &_tel,
    &_address {
        position: absolute;
        font-size: 20px;
        font-weight: 700;
    }
    &_tel {
        margin-top: 90px;
        &.contact_tel-two {
            margin-top: 110px;
        }
    }
    &_address {
        margin-top: 165px;
    }
    #map{
        height:100%;
        width: 100%;
        position: absolute; top: 0; left: 0;
    }
}