br.mob {
    display: none;
}
.travel_h3 {
    display: none;
}
@media screen and (min-width:1200px) {
    .intro_title {
        br {content: ""}
    }
    .travel_two {
        br {content: ""}
    }
}
@media screen and (min-width:992px) {
    
}
@media screen and (max-width:1200px) {
    .header {
        &_logo {
            padding-top: 28px;
        }
        &_nav {
            padding-top: 55px;
            &--li {
                margin: 0 20px;    
            }
            &--a {
                font-size: 12px;
            }
        }
        &_tel {
            padding-top: 40px;
            &--number {
                font-size: 15px;
            }
            &--callback {
                margin-top: 7px;
                font-size: 12px;
                padding: 3px 33px;
            }
        }
    }
    .intro {
        &_h1 {
            font-size: 65px;
            margin-top: 65px;
        }
        &_title {
         font-size: 28px;
        }
    }
    .trust {
        &_title {
            margin-top: 55px;
        }
        &_item {
            &--text {
                top: -30px;
                font-size: 25px;
            }
        }
    }
    .about {
        background: url(../img/about.png) no-repeat;
        background-position: center center;
        background-size: cover;
        margin-top: 100px;
        &_left {
            padding: 60px 0 50px;
            max-width: 460px;
            br {
                display: none;
                &.one {
                    display: block;
                }
            }
            p {
                font-size: 23px;
            }
        }
    }
    .money {
        &_title {
            margin: 80px auto 30px;
            font-size: 65px;
        }
        &_desc {
            font-size: 29px;
        }
        &_items {
            margin-top: 45px;
            display: flex;
            justify-content: space-between;
        }
        &_li {
            padding-left: 60px;
            margin: 7px 0;
            position: relative;
            &::after {
                content: '';
                position: absolute;
                top: 0; left: 0;
                background: url(../img/li.png) no-repeat;
                background-size: cover;
                width: 30px;
                height: 26px;
            }
            p {
                font-size: 24px;
            }
        }
        .money_subtitle {
            margin: 40px auto 25px;
            font-size: 50px;
        }
    }

    .form {
        &_input {
            &::placeholder {
                font-size: 22px;
            }
        }
        &_btn {
            font-size: 21px;
        }
    }
    
    .travel {
        &_title {
            margin-top: 75px;
        }
        &_content {
            position: relative;
            min-height: 505px;
            &::after {
                content: '';
                position: absolute;
                top: -10px; left: 50%;
                transform: translate(-50%, 0%);
                background: url(../img/travel.png) no-repeat;
                background-size: cover;
                width: 541px;
                height: 468px;
                z-index: -1;
            }
        }
        &_first,
        &_two {
            &--item {
                font-size: 23px;
            }
        }
        &_first {
            min-height: 170px;
            margin-top: 32px;
            &--item {
                &:nth-child(1) {
                    max-width: 185px;
                    margin-left: 50px;
                }
                &:nth-child(2) {
                    margin-left: 20px;
                }
                &:nth-child(3) {
                    margin-right: 10px;
                }
            }
        }
        &_two {
            min-height: 200px;
            align-items: flex-end;
            &--item {
                &:nth-child(1) {
                    max-width: 260px;
                }
                &:nth-child(2) {
                    max-width: 255px;
                    position: relative;
                    top: 5px; left: 20px;
                }
            }
        }
    }
    
    .expert {
        &_title {
            margin-top: 70px;
        }
        &_desc {
            margin: 25px auto;
            font-size: 25px;
        }
        &_text {
            font-size: 28px;
        }
    }
    .of {
        &_title {
            margin: 70px auto 50px;
            font-size: 48px;
        }
    }
    .reviews {
        margin-top: 70px;
        &_title {
            padding-top: 30px;
            font-size: 65px;
        }
        &_content {
            margin: 15px auto 0;
            iframe {
                width: 100%;
            }
            .last {
                margin-top: 23px;
            }
        }
        &_bottom {
            margin: 30px auto 35px;
        }
    }
    .contact {
        &_content {
            margin-top: 55px;
            min-height: 272px;
            min-width: 375px;
        }
        &_title {
            margin-top: 20px;
            font-size: 38px;
        }
        &_tel,
        &_address {
            font-size: 21px;
        }
    }
}
