@media screen and (max-width:600px) {
    .about {
        margin-top: 40px;
        &_wrap {
            position: relative;
        }
        &_left {
            padding: 100px 15px 60px;
            z-index: 2;
            br.mob {
                display: block;
            }
            p {
                font-size: 12px;
                position: relative;
                top: 30px;
            }
        }
        &_right {
            position: absolute;
            right: 0; bottom: 0;
            z-index: 1;
            background: url(../img/girl-mob.png) no-repeat;
            width: 211px;
            height: 355px;
            background-position: center center;
            img {
                display: none;
            }
        }
    }
    .money {
        br.mob {
            display: block;
        }
        &_title {
            margin: 45px auto 15px;
            font-size: 27px;
        }
        &_desc {
            font-size: 12px;
            line-height: 18px;
        }
        &_items {
            margin-top: 25px;
            flex-wrap: wrap;
        }
        &_right {
            .money_li:first-child {
                margin: 0;
            }
        }
        &_li {
            padding-left: 40px;
            margin: 5px 0;
            position: relative;
            &::after {
                content: '';
                position: absolute;
                top: 0; left: 0;
                background: url(../img/li.png) no-repeat;
                background-size: cover;
                width: 19px;
                height: 16px;
            }
            p {
                font-size: 14px;
            }
        }
        .money_subtitle {
            margin: 25px auto 10px;
            font-size: 25px;
        }
    }

    .form {
        &_wrap {
            flex-wrap: wrap;
            justify-content: center;
        }
        &_input {
            margin: 6px 0;
            width: 80%;
            &::placeholder {
                font-size: 14px;
            }
        }
        &_btn {
            font-size: 15px;
            padding: 6px;
            margin-top: 12px;
        }
    }

    .travel {
        &_title {
            margin-top: 35px;
            font-size: 27px;
        }
        &_content {
            position: relative;
            min-height: 212px;
            &::after {
                content: '';
                position: absolute;
                top: -15px; left: 50%;
                transform: translate(-50%, 0%);
                background: url(../img/travel.png) no-repeat;
                background-size: cover;
                width: 245px;
                height: 212px;
                z-index: -1;
            }
        }
        &_first,
        &_two {
            &--item {
                font-size: 11px;
            }
        }
        &_first {
            min-height: 100px;
            margin-top: 10px;
            &--item {
                &:nth-child(1) {
                    min-width: 100px;
                    margin-left: 0;
                    position: relative;
                    top: 10px;
                    left: 30px;
                }
                &:nth-child(2) {
                    text-align: center;
                    margin-left: 0px;
                    min-width: 114px;
                }
                &:nth-child(3) {
                    text-align: right;
                    margin-right: 0;
                    margin-left: 0px;
                    position: relative;
                    top: -15px;
                }
            }
        }
        &_two {
            min-height: 110px;
            align-items: flex-end;
            &--item {
                &:nth-child(1) {
                    max-width: 120px;
                }
                &:nth-child(2) {
                    max-width: 255px;
                    position: relative;
                    top: 0px; left: 0px;
                    text-align: right;
                }
            }
        }
        &_form {
            margin-top: 20px;
        }
        &_h3 {
            display: block;
            text-align: center;
            margin-top: 25px;
            color: #b72b45;
            font-size: 19px;
            font-weight: 700;
        }

    }

    .expert {
        &_title {
            margin-top: 35px;
            font-size: 27px;
        }
        &_desc {
            margin: 10px auto 0px;
            font-size: 14px;
        }
        &_h3 {
            margin: 25px auto 15px;
            font-size: 20px;
        }
        &_text {
            font-size: 15px;
        }
        &_wrap {
            flex-wrap: wrap;
        }
        &_item {
            margin: 10px 0;
            &--btn {
                font-size: 20px;
                padding: 5px 30px;
            }   
        }
    }
    .of {
        &_title {
            margin: 85px auto 10px;
            font-size: 25px;
        }
        img {
            margin: 5px 0;
        }
    }
    .reviews {
        margin-top: 30px;
        &_title {
            padding-top: 15px;
            font-size: 25px;
        }
        &_content {
            margin: 15px auto 0;
            iframe {
                width: 100%;
            }
            .last,
            .middle {
                margin-top: 15px;
            }
        }
        &_bottom {
            margin: 20px auto;
        }
        &_btn {
            font-size: 11px;
            padding: 7px 12px;
            width: 180px;
            &:last-child {
                margin-left: 0;
                margin-top: 10px;
            }
        }
    }
    .contact {
        min-height: 365px;
        &_content {
            margin-top: 55px;
            min-width: 290px;
            min-height: 200px;
        }
        &_title {
            position: absolute;
            margin-top: 20px;
            font-size: 29px;
        }
        &_tel,
        &_address {
            font-size: 16px;
        }
        &_tel {
            margin-top: 90px;
            &.contact_tel-two {
                margin-top: 110px;
            }
        }
        &_address {
            margin-top: 165px;
            br.mob {
                display: block;
            }
        }
    }
    
}
